<template>
	<view class="content">
		 <!-- 用户自定义头像 -->
		 <view class="userPortrait">
			  <image :src="yonghuwx.avatarUrl" mode="widthFix"></image>
		 </view>
		 <view class="userLook" >
			  <!-- 用户自定义名字 -->
			  <view class="user-title clamp">{{yonghuwx.nickName}}</view>
			  <button open-type="getUserInfo">获取用户信息</button>
			  <button open-type="getPhoneNumber" @getphonenumber="getPhoneNumber()">获取用户手机号</button>
		 </view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				yonghuwx:[]
			}
		},
		onLoad() {
			console.log("666")
			let that = this
			uni.getUserInfo({
				provider:'weixin',
				success:function(infoRes){
					console.log(infoRes)
					that.yonghuwx = infoRes.userInfo
					console.log(that.yonghuwx)
					// if(infoRes){
					// 	uni.navigateTo({
					// 	    url: '../enter_products/enter_products'
					// 	});
					// }
				},
				fail:function(res){
					console.log(res)
				}
			});
		},
		methods: {
			　getPhoneNumber: function (e) {
				  console.log("666",e)
			      var ivObj = e.detail.iv
			      var telObj = e.detail.encryptedData
			      var codeObj = "";
			      var that = this;
			      //------执行Login---------
			      wx.login({
			        success: res => {
			          console.log('code转换', res.code);
			 
			　　　　　　//用code传给服务器调换session_key
			          wx.request({
			            url: 'https://你的接口文件路径', //接口地址
			            data: {
			              appid: "getphonenumber",
			              secret: "1443549d5b882b7a74c7c6f62eb90ded",
			              code: res.code,
			              encryptedData: telObj,
			              iv: ivObj
			            },
			            success: function (res) {
			              phoneObj = res.data.phoneNumber;
			              console.log("手机号=", phoneObj)
			              wx.setStorage({   //存储数据并准备发送给下一页使用
			                key: "phoneObj",
			                data: res.data.phoneNumber,
			              })
			            }
			          })
			 
			          //-----------------是否授权，授权通过进入主页面，授权拒绝则停留在登陆界面
			          if (e.detail.errMsg == 'getPhoneNumber:user deny') { //用户点击拒绝
			            wx.navigateTo({
			              url: '../index/index',
			            })
			          } else { //允许授权执行跳转
			            wx.navigateTo({
			              url: '../test/test',
			            })
			          }
			        }
			      });
				}
		}
	}
</script>

<style lang="stylus" scoped>
	
</style>
